<template>
  <div class="ai-rate-group">
    <div class="ai-rate-group__title">
      <ai-title :style="titleStyle" :index="index" :content="titleContent" />
    </div>
    <div class="ai-rate-group__list">
      <ai-rate
        :key="i"
        v-for="(v, i) in options"
        v-model="checkedMap[v.value]"
        :title-content="v.label"
        :max="max"
        :show-text="showText"
        :texts="texts"
        @input="handleInput"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: 'AiRateGroup',
  props: {
    // 
    index: {
      type: [Number, String],
      default: ''
    },
    // 
    value: {
      type: Object,
      default: () => ({})
    },
    // 
    // [{ label: '' , value: 0 }]
    options: {
      type: Array,
      default: () => []
    },
    // 
    titleStyle: {
      type: [Object, Array, undefined],
      default: undefined
    },
    // 
    titleContent: {
      type: String,
      default: ''
    },
    // 
    max: {
      type: [Number, String],
      default: 10
    },
    //  texts 
    showText: {
      type: Boolean,
      default: false
    },
    // 
    texts: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      checkedMap: {}
    }
  },
  watch: {
    value: {
      handler(val) {
        this.checkedMap = { ...val }
      },
      immediate: true
    }
  },
  methods: {
    handleInput() {
      this.$emit('input', this.checkedMap)
    }
  }
}
</script>
